<template>
<v-layout class="carousel">
  <v-carousel v-if="template && template.subitems && template.subitems.length"
              :height="200"
              class="elevation-0"
              hide-controls
              :cycle='cycle'>
    <v-carousel-item
      v-for="(item,i) in template.subitems"
      :key="i"
      @click.stop="click(item)"
      :src="item.img"
    />
  </v-carousel>
</v-layout>
</template>

<script>
  export default {
    name: "CarouselTemplate",
    data() {
      return {
        cycle: true
      }
    },

    props:{
      template: null,
    },
    methods:{
      click(){

      }
    },

    activated(){
      this.$set(this,'cycle',true)
    },

    deactivated(){
      this.$set(this,'cycle',false)
    }
  }
</script>

<style lang="less">
.carousel {
  .v-carousel__controls {
    justify-content: center;
    height: 16px;
    width: auto;
    background: rgba(51, 51, 51, 0.473);
    border-radius: 8px;
    left: 50%;
    bottom: 2%;
    transform: translate(-50%,0)
  }

  .v-carousel__controls__item .v-icon {
    opacity: 1;
  }

  .v-carousel__controls__item:hover {
    opacity: 0%;
  }

  .v-carousel__controls__item:hover .v-icon {
    background: none;
    color: rgba(12, 132, 255, 1);
    transition: none;
  }

  .v-carousel__controls__item {
    margin: 0 0px !important;
  }
}
</style>



