export function formatSku(skuInfo) {
  if(skuInfo && skuInfo.skus && skuInfo.skus.length){
    let skuMap = {}

    skuInfo.skus.forEach((item)=>{
      skuMap[item.skuId] = item
    })

    skuInfo.skuMap = skuMap
    return skuInfo
  }else {
    return null
  }
}

export function addressListFormat (addressList) {
  if (!addressList || addressList.length <= 0) {
    return
  }

  let chosenAddress = null

  addressList.forEach(item => {
    item.id = item.addressId
    item.tel = item.phone
    item.address = `${item.detail} ${item.city ? item.city : ''} ${item.state ? item.state : ''} ${item.country}`
    if (item.isDefault) {
      chosenAddress = item
    }
  })

  chosenAddress = chosenAddress ? chosenAddress : addressList[0]
  return {addressList, chosenAddress}

}



