import Vue from 'vue'
import {getAddress} from "./Net";

function setUserToken(user) {
  Vue.http.headers.common['X-Amz-Security-Token'] = user.token
  window.midasConfig.userInfo = user
}

export async function getUserInfo() {
  if(process.env.NODE_ENV!=="production" && !window.midasConfig.userInfo){
    setUserToken({
      token:"TOKEN|F2672290072543C1801B35A16FACA528"
    })
  }


  if(!Vue.http.headers.common['X-Amz-Security-Token']){
    login()
  }else if (!window.midasConfig.userInfo.address) {
    await refreshAddress()
  }

  return window.midasConfig.userInfo
}

async function refreshAddress() {
  window.midasConfig.userInfo.addresses =  await getAddress()
}

function login() {
  throw new Error('please login first')
}



