<template>
  <div>
    <v-toolbar color="primary" dark fixed app>
      <v-btn icon @click="$router.back()">
        <v-icon>arrow_back_ios</v-icon>
      </v-btn>
      <v-toolbar-title>Order</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon>search</v-icon>
      </v-btn>
    </v-toolbar>

    <v-tabs
      dark
      color="primary"
      show-arrows
    >
      <v-tabs-slider color="white" ></v-tabs-slider>

      <v-tab
        v-for="(statusId,index) in statuses"
        :key="index"
        v-model="chosenStatusId"
      >
        <div style="font-size: 10px">
          {{statusById(statusId)}}
        </div>

      </v-tab>

      <v-tabs-items>
        <v-tab-item
          v-for="(statusId,index) in statuses"
          :key="index"
          lazy
        >
          <OrderListTabItem :statusId="statusId"/>
        </v-tab-item>
      </v-tabs-items>
    </v-tabs>

  </div>
</template>

<script>
  import OrderListTabItem from "@/components/page/OrderList/template/prod/OrderListTabItem"
  export default {
    name: "prod",
    components:{OrderListTabItem},
    data(){
      return {
        chosenStatusId:'WAIT_BUYER_PAY',
        statuses: ['WAIT_BUYER_PAY', 'WAIT_SELLER_SEND_GOODS', 'WAIT_BUYER_CONFIRM_GOODS', 'ALL_CLOSED', 'TRADE_BUYER_SIGNED'],
      }
    },
    methods:{
      statusById (id) {
        if (id) {
          switch (id) {
            case 'WAIT_BUYER_PAY':
              return this.$t('wait-buyer-pay')
            case 'WAIT_SELLER_SEND_GOODS':
              return this.$t('wait-seller-send-goods')
            case 'WAIT_BUYER_CONFIRM_GOODS':
              return this.$t('wait-buyer-confirmation')
            case 'ALL_CLOSED':
              return this.$t('order-canceled')
            case 'TRADE_BUYER_SIGNED':
              return this.$t('order-completed')
            default:
              return ''
          }
        }

        return ''
      }
    }
  }
</script>

<style scoped>

</style>



