<template>
  <div id="app">
    <v-app>
      <v-content>
        <navigation>
          <router-view></router-view>
        </navigation>
      </v-content>
    </v-app>
  </div>
</template>

<script>
  export default {
    name: 'app',
    created() {
      this.$navigation.on('forward', (to, from) => {
        midasLog.debug('forward to', to, 'from ', from)
      })
      this.$navigation.on('back', (to, from) => {
        midasLog.debug('back to', to, 'from ', from)
      })
      this.$navigation.on('replace', (to, from) => {
        midasLog.debug('replace to', to, 'from ', from)
      })
      this.$navigation.on('refresh', (to, from) => {
        midasLog.debug('refresh to', to, 'from ', from)
      })
      this.$navigation.on('reset', () => {
        midasLog.debug('reset')
      })
      // and use [once, off] methods
      this.$navigation.once('forward', () => {
        midasLog.debug('appear once')
      })
      const off = () => {
        midasLog.debug('will not appear')
      }
      this.$navigation.on('forward', off)
      this.$navigation.off('forward', off)
    },
    mounted() {
      window.addEventListener('popstate', function (e) {
        if (!e) {
          return
        }
        if (e.currentTarget.document.URL.indexOf(`#/?`) !== -1 && window.showHome) {
          history.pushState(null, null, document.URL)
          window.WebViewJavascriptBridge.send({
              'method': 'close'
            }
          )
        }
      }, false)
    }
  }
</script>

<style lang="less">
  .index_item {
    .index_content {
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 48px;
      text-align: center;
      font-size: @title_size_font;
      background: white;
    }

    .index_flag {
      height: 24px;
      width: 3px;
      margin-right: 9px;
      background: @primary_color;
    }
  }

  .bottom_button_container{
    height: @bottom_button_height;
    width: 100%;
    z-index: 1;
    position: fixed;
    bottom: 0;
    display: flex;
    background: white;
    justify-content: center;
    align-items: center;
    padding: 12px;
    .v-btn {
      height: 100%;
    }
  }
</style>



