<template>
  <refresh-and-load-more>
    <template slot-scope="loadData" >
      <div style="overflow-x:scroll;overflow-y: scroll;max-width: 500px">
        <OrderItem v-for="(order,i) in loadData.items" :key="i" :order="order"/>
      </div>
    </template>
  </refresh-and-load-more>
</template>

<script>
  import RefreshAndLoadMore from '@/components/view/RefreshAndLoadMore'
  import NetWorkUtils from '@/util/Net'
  import OrderItem from "./OrderItem"

  export default {
    name: "TabOrderList",
    components:{RefreshAndLoadMore,OrderItem},
    props: ['statusId'],

    methods:{
      async loadDataByPage(page, pageSize) {
        let req = {}
        req.pageNo = page
        req.pageSize = pageSize
        req.status = this.statusId
        let info = await NetWorkUtils.post('/order/query/v2', req)
        return info.orders
      },
    },
  }
</script>

<style scoped>

</style>



