<script>
  import {getUserInfo} from '@/util/DataUtil'
  import getTemplate from '@/components/TemplateComponent'
  const componentName = 'OrderList'
  export default {
    extends:getTemplate(`page/${componentName}`),
    name:componentName,
    async mounted(){
      await getUserInfo()
    }
  }
</script>



