<template>
  <v-dialog
    :value="value" @input="onInput"
    width="300"
  >
    <v-card
      color="primary"
      dark
    >
      <v-card-text>
        Please stand by
        <v-progress-linear
          indeterminate
          color="white"
          class="mb-0"
        ></v-progress-linear>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    name: "Loading",
    props: {
      value: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      onInput(val) {
        this.$emit('input', val);
      },
    }
  }
</script>

<style scoped>

</style>



