<template>
  <v-dialog
    :max-width="150"
    v-model="show"
    hide-overlay
    persistent
  >
   <div style="padding: 10px;background: #212121;opacity:0.66;color: white;display: flex;">
     <div style="margin: auto;word-break: break-word;font-size: 12px">
       {{value}}
     </div>
   </div>
  </v-dialog>
</template>

<script>
  let time;
  export default {
    name: "Toast",
    props: {
      value:{
        type:String,
        default: ""
      }
    },
    methods: {
      onInput(val) {
        this.$emit('input', val);
      },
    },
    data(){
      return {
        show:false
      }
    },
    watch:{
      value(newValue){
        if(time){
          clearTimeout(time)
          time = null
        }

        if(newValue){
          this.$set(this,'show',true)
          time = setTimeout(()=>{
            this.onInput(null)
          },1500)
        }else {
          this.$set(this,'show',false)
        }
      }
    }
  }
</script>

<style scoped>

</style>



