<template>
  <v-bottom-sheet full-width :hide-overlay="hideOverlay" :lazy="lazy" scrollable :value="value" @input="onInput">
    <div class="sku">
      <div style="display: flex;padding: 14px">
        <v-img
          :src="chosenImg?chosenImg:defaultHeader"
          aspect-ratio="1"
          style="border-radius: 4px"
          width="88px"
          class="grey lighten-2"
        >
          <v-layout
            slot="placeholder"
            fill-height
            align-center
            justify-center
            ma-0
          >
            <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
          </v-layout>
        </v-img>

        <div style="width: 100%;padding-left: 10px;">
          <div class="sku_title">
            {{title}}
          </div>

          <div v-if="selectSkuStructure" class="sale_price">
            {{'$'+selectSkuStructure.salePrice}}
          </div>

          <div class="left">
            Left{{selectSkuStructure?selectSkuStructure.inventory:0}}
          </div>
        </div>

        <v-btn flat icon small color="grey" style="margin: 0" @click="onInput(false)">
          <v-icon small>close</v-icon>
        </v-btn>
      </div>

      <div :style="{'max-height': maxHeight+'px','overflow-y': 'scroll',background: 'white'}">
        <div v-if="sku && sku.skuStructures && sku.skuStructures.length">
          <div style="margin-left: 10px;margin-right: 10px" v-for="skuStructure in sku.skuStructures">
            <div class="category">{{skuStructure.category}}</div>

            <v-layout row wrap v-if="skuStructure.skuProps && skuStructure.skuProps.length">
              <div v-for="skuProp in skuStructure.skuProps"
                   @click="chosenProp(skuStructure.category,skuProp)"
                   class="prop lighten-5" :class="activeProp(skuStructure.category,skuProp.skuPropId)">
                {{skuProp.text}}
              </div>
            </v-layout>
          </div>
        </div>

        <div class="inventory">
          <div class="quantity_title">Quantity:</div>
          <div class="counter_num">
            <v-btn class="elevation-0 counter" :class="{active_counter:minusActive}" flat :disabled="!minusActive"
                   @click="minus">
              <v-icon :color="minusActive?'black':'grey'" small>remove</v-icon>
            </v-btn>
            <v-divider vertical/>
            <div style="line-height: 36px" class="counter active_counter">
              {{selectSkuStructure?selectNumMap[selectSkuStructure.skuId]:0}}
            </div>
            <v-divider vertical/>
            <v-btn class="elevation-0 counter" :class="{active_counter: plusActive}" flat :disabled="!plusActive"
                   @click="plus">
              <v-icon :color="plusActive?'black':'grey'" small>add</v-icon>
            </v-btn>
          </div>
        </div>
      </div>

      <div>
        <slot v-bind:click="skuBuy"/>
      </div>
    </div>


  </v-bottom-sheet>
</template>

<script>
  export default {
    name: "Sku",
    data() {
      return {
        chosenMap: {},
        selectSkuStructure: null,
        chosenImg: null,
        selectNumMap: {},
      }
    },
    props: {
      maxHeight: {
        type: Number,
        default: 500
      },
      lazy: {
        type: Boolean,
        default: false
      },
      hideOverlay: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: 'title'
      },
      defaultHeader: {
        type: String,
        default: 'https://picsum.photos/500/300?image=12'
      },
      sku: {
        type: Object,
      },
      value: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      onInput(val) {
        this.$emit('input', val);
      },

      chosenProp(category, skuProp) {
        this.$set(this.chosenMap, category, {skuPropId: skuProp.skuPropId, text: skuProp.text})
        if (skuProp.icon) {
          this.$set(this, 'chosenImg', skuProp.icon.url)
        }

        let keys = Object.keys(this.chosenMap)

        if (keys && keys.length === this.sku.skuStructures.length) {
          this.setChosenSku(this.chosenMap)
        }
      },

      initSku(sku) {
        if (sku && sku.skuStructures && sku.skuStructures.length) {
          sku.skuStructures.forEach(skuStructure => {
            if (skuStructure && skuStructure.skuProps && skuStructure.skuProps.length) {
              this.chosenProp(skuStructure.category, skuStructure.skuProps[0])
            }
          })
        } else {
          this.setChosenSku()
        }
      },

      activeProp(category, propId) {
        return {
          active_prop: this.chosenMap[category].skuPropId === propId,
          primary: this.chosenMap[category].skuPropId === propId,
          'primary--text':this.chosenMap[category].skuPropId === propId,
        }
      },

      setChosenSku(map) {
        if (map && this.sku && this.sku.skus) {
          for (let i = 0; i < this.sku.skus.length; i++) {
            let temp = this.sku.skus[i]
            let flag = true

            for (let key in map) {
              flag = temp.skuPropIds.indexOf(map[key].skuPropId) >= 0
              if (!flag) {
                break
              }
            }

            if (flag) {
              let count = this.selectNumMap[temp.skuId]
              if (!count) {
                this.$set(this.selectNumMap, temp.skuId, temp.inventory ? 1 : 0)
              }

              let selectSkuStructure = {
                ...temp,
                category: {
                  ...map
                }
              }
              this.$set(this, 'selectSkuStructure', selectSkuStructure)
              return
            }
          }
        } else if (this.sku && this.sku.skus) {
          this.$set(this, 'selectSkuStructure', this.sku.skus[0])
          this.$set(this.selectNumMap, this.sku.skus[0].skuId, this.sku.skus[0].inventory ? 1 : 0)
        }
      },
      minus() {
        let count = this.selectNumMap[this.selectSkuStructure.skuId]
        if (count > 1) {
          this.$set(this.selectNumMap, this.selectSkuStructure.skuId, count - 1)
        }
      },

      plus() {
        let count = this.selectNumMap[this.selectSkuStructure.skuId]
        if (count < this.selectSkuStructure.inventory) {
          this.$set(this.selectNumMap, this.selectSkuStructure.skuId, count + 1)
        }
      },
      skuBuy() {
        this.onInput(false)
        if (this.selectSkuStructure && this.selectNumMap[this.selectSkuStructure.skuId]) {
          let buyStructure = Object.assign(this.selectSkuStructure)
          buyStructure.selectNum = this.selectNumMap[this.selectSkuStructure.skuId]
          this.$emit('skuBuy', buyStructure);
        }
      }

    },

    computed: {
      plusActive() {
        return this.selectSkuStructure ? (this.selectNumMap[this.selectSkuStructure.skuId] < this.selectSkuStructure.inventory) : false
      },

      minusActive() {
        return this.selectSkuStructure ? (this.selectNumMap[this.selectSkuStructure.skuId] > 1) : false
      },
    },

    watch: {
      sku(newVal) {
        this.initSku(newVal)
      },
    },

    mounted() {
      this.initSku(this.sku)
    }
  }
</script>

<style scoped lang="less">
  .sku {
    background: white;
    border-radius: 8px;

    .inventory {
      display: flex;
      flex-direction: row;
      padding: 10px 10px 0;
      margin-bottom: 28px;
      justify-content: space-between;
      .quantity_title {
        font-size: @title_size_font;
        font-weight: bold;
        line-height: 36px;
      }
      .counter_num {
        border: 1px solid lightgrey;
        border-radius: 2px;
        width: 112px;
        display: flex;
        text-align: center;
        .counter {
          margin: 0;
          width: 36px;
          height: 36px;
          padding: 0;
          min-width: 36px;
          background: @background_grey;
        }

        .active_counter {
          background: white;
        }
      }
    }

    .sku_title {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      height: @desc_size_font*2+1;
      margin-bottom: 5px;
      margin-top: 5px;
      line-height: @desc_size_font;
      font-size: @desc_size_font;
      color: @font_gray;
    }

    .sale_price {
      font-size: @title_size_font;
      margin-bottom: 5px;
      font-weight: bold;
    }

    .left {
      font-size: @desc_size_font;
      color: @font_gray;
    }

    .category{
      font-size: @title_size_font;
      margin-bottom: 10px;
      font-weight: bold;
    }

    .prop {
      padding: 8px 13px 8px 13px;
      margin-right: 15px;
      margin-bottom: 10px;
      min-width: 68px;
      border-radius: 4px;
      border: 1px solid #ECECEC;
      text-align: center;
      background: #ECECEC;
      font-size: @desc_size_font;
    }

    .active_prop {
      border-radius: 4px;
      border: 1px solid #0C84FF;
      border-color: #0C84FF !important;
    }
  }
</style>



