<template>
  <div >
    <component style="margin-bottom: 10px" v-if="categories && categories.length" v-for="(category,i) in categories" :key="i" :is="getComponentByType(category.type)" :template="category"/>
  </div>
</template>

<script>
  import CarouselTemplate from "@/components/page/Home/template/prod/CarouselTemplate"
  import CategoryTemplate from "@/components/page/Home/template/prod/CategoryTemplate"
  import DailyTemplate from "@/components/page/Home/template/prod/DailyTemplate"

  export default {
    name: "TemplateContainer",
    components:{
      CarouselTemplate,
      CategoryTemplate,
      DailyTemplate
    },
    props:{
      categories:null
    },
    methods:{
      getComponentByType(type){
        switch (type) {
          case 'carousel':
            return CarouselTemplate
          case 'category':
            return CategoryTemplate
          case 'daily':
            return DailyTemplate
          default:
            return null

        }
      }
    },
  }
</script>

<style scoped>

</style>



