import VueResource from 'vue-resource'
import Vue from 'vue'
import {addressListFormat} from '@/util/Format'

Vue.use(VueResource)
const host = process.env.NODE_URL

Vue.http.interceptors.push(function (request, next) {
  midasLog.debug("request", request)
  next()
})
const NetWorkUtils = {
  /**
   * GET 
   * @param url
   * @returns {Promise}
   */
  get: function (url) {
    url = url.indexOf('http') < 0 ? host + url : url
    return new Promise((resolve, reject) => {
      Vue.http.get(url)
        .then((res) => {
          resolve(parseBody(res.body))
        })
        .catch((res) => {
          if (res.body) {
            res.code = -1
            res.msg = res.body
          }
          midasLog.error('get error ==', res)
          reject(res)
        })
    })
  },

  /**
   * post 
   * @param url
   * @param params
   * @returns {Promise}
   */
  post: function (url, params) {
    let start = new Date()
    url = url.indexOf('http') < 0 ? host + url : url
    return new Promise((resolve, reject) => {
      Vue.http.post(url, {'data': params})
        .then((res) => {
          resolve(parseBody(res.body))
          midasLog.debug(url + ",cost time:" + (new Date() - start) / 1000)
        })
        .catch((res) => {
          if (res.body) {
            res.code = -1
            res.msg = res.body
          }
          midasLog.debug('post error res', res)
          midasLog.debug(url + "const time:" + (new Date() - start) / 1000)
          reject(res)
        })
    })
  }
}

function parseBody(body) {
  midasLog.debug("body", body)
  if (body != null && body.code === 0) {
    return body.data
  } else {
    let error = {}
    if (body != null && body.code > 0) {
      error.code = body.code
      error.msg = body.msg
    } else {
      error.code = -1
      error.msg = 'unknown'
    }

    throw error
  }
}

export async function getAddress() {
  try {
    let info = await NetWorkUtils.post('/address/list/v1')
    return addressListFormat(info.addresses)
  } catch (e) {
    midasLog.error(e)
  }
  return null
}

export async function productList(req) {
  try {
    let info = await NetWorkUtils.post('/product/list/v2',req)
    if(info){
      return info.products
    }
  } catch (e) {
    midasLog.error(e)
  }
  return null
}

export async function getTemplate(appId) {
  try {
    let info = await NetWorkUtils.post('/test/template/get/v1',{appId})
    return info
  } catch (e) {
    midasLog.error(e)
  }
  return null
}

export default NetWorkUtils



