<template>
  <div class="van-list">
    <slot />
    <div
      v-show="loading"
      class="loading"
    >
      <v-layout class="loading" fill-height align-center justify-center>
        <slot name="loading">
          <v-progress-circular indeterminate color="grey" :size="16" :width="2"></v-progress-circular>
          <v-flex shrink ml-3 class="loading-text">{{ loadingText || 'Loading' }}</v-flex>
        </slot>
      </v-layout>
    </div>
  </div>
</template>

<script>
  import utils from '../../util/scroll';
  import { on, off } from '../../util/event';

  export default({
    name: 'LoadMore',

    model: {
      prop: 'loading'
    },

    props: {
      loading: Boolean,
      finished: Boolean,
      immediateCheck: {
        type: Boolean,
        default: true
      },
      offset: {
        type: Number,
        default: 300
      },
      loadingText: String
    },

    mounted() {
      this.scroller = utils.getScrollEventTarget(this.$el);
      this.handler(true);

      if (this.immediateCheck) {
        this.$nextTick(this.check);
      }
    },

    destroyed() {
      this.handler(false);
    },

    activated() {
      this.handler(true);
    },

    deactivated() {
      this.handler(false);
    },

    watch: {
      loading() {
        this.$nextTick(this.check);
      },

      finished() {
        this.$nextTick(this.check);
      }
    },

    methods: {
      check() {
        if (this.loading || this.finished) {
          return;
        }
        const el = this.$el;
        const { scroller } = this;
        const scrollerHeight = utils.getVisibleHeight(scroller);

        /* istanbul ignore next */
        if (!scrollerHeight || utils.getComputedStyle(el).display === 'none' || el.offsetParent === null) {
          return;
        }

        const scrollTop = utils.getScrollTop(scroller);
        const targetBottom = scrollTop + scrollerHeight;

        let reachBottom = false;

        /* istanbul ignore next */
        if (el === scroller) {
          reachBottom = scroller.scrollHeight - targetBottom < this.offset;
        } else {
          const elBottom =
            utils.getElementTop(el) -
            utils.getElementTop(scroller) +
            utils.getVisibleHeight(el);
          reachBottom = elBottom - scrollerHeight < this.offset;
        }

        /* istanbul ignore else */
        if (reachBottom) {
          this.$emit('input', true);
          this.$emit('load');
        }
      },

      handler(bind) {
        /* istanbul ignore else */
        if (this.binded !== bind) {
          this.binded = bind;
          (bind ? on : off)(this.scroller, 'scroll', this.check);
        }
      }
    }
  });
</script>

<style scoped lang="less">
  .van-list {
    .loading {
      text-align: center;

      .van-loading,
      .text {
        display: inline-block;
        vertical-align: middle;
      }

      .van-loading {
        width: 16px;
        height: 16px;
        margin-right: 5px;
      }

      .text {
        font-size: 13px;
        color: black;
        line-height: 50px;
      }
    }
  }
</style>



