<template>
  <div style="background: white">
    <v-img
      :src="product.galleryImgs[0].url"
      aspect-ratio="1"    />

    <v-card-text style="padding: 10px">
      <div class="text">
        {{product.title}}
      </div>
      <div style="font-size: 13px">
        <span style="font-size: 18px;font-weight: bold">${{product.salePrice}}</span>
        <span style="text-decoration: line-through;color: lightgray" v-if="product.originalPrice">${{product.originalPrice}}</span>
      </div>
    </v-card-text>
  </div>
</template>

<script>
  export default {
    name: "ProductItem",
    props: ['product'],
  }
</script>

<style scoped lang="less">
  .text {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    height: 28px;
    line-height: 14px;
    font-size: 14px;
    color: @font_gray;
  }
</style>



