<template>
  <div style="background: white" class="order-item">

    <div class="product">
      <div class="product-detail">
        <img :src="order.purchaseDetails[0].productIcon">
        <div style="flex-grow: 1">
          <div class="desc">
            <span v-if="order.status !== 'WAIT_BUYER_PAY'" class="status-flag" :class="statusClass">{{stateDesc}}</span>
            {{order.purchaseDetails[0].title}}
          </div>
          <div class="sku">{{skuDesc}}</div>
          <div>
            <div class="sale-price">
              ${{order.purchaseDetails[0].sku.salePrice}}
            </div>
          </div>
        </div>
        <div style="color: gray;">x{{order.purchaseDetails[0].num}}</div>
      </div>

      <div class="product-price">
        <div>
          <div>Shipping:</div>
          <span>
              ${{order.purchaseDetails[0].shipping.price}}
            </span>
        </div>

        <div>
          <div style="margin-top: 0.5rem;">Total:</div>
          <span class="price-total">
              ${{order.payment}}
            </span>
        </div>

      </div>

      <div style="width: 100%;text-align: center">
        <button class="button">{{buttonDesc}}</button>
      </div>

    </div>
    <!--<img style="height: 90px;width: 90px;" class="d-block" :src="order.picUrl">-->
    <!--<div class="align-self-center h-75 w-100 pl-2 d-flex flex-column justify-content-between">-->
    <!--<span style="font-size: 14px">{{order.title}}</span>-->
    <!--<span class="d-block font-weight-light" style="color: gray;font-size: 13px">{{skuDesc}}</span>-->
    <!--<div class="d-flex flex-row h-25 align-items-center justify-content-between"-->
    <!--style="font-size:0.9rem">-->
    <!--<span class="font-weight-light" style="color: red">${{order.payment}}</span>-->
    <!--<span class="font-weight-light ">X{{order.purchaseDetails[0].num}}</span>-->
    <!--&lt;!&ndash;d-inline btn btn-outline-danger btn-sm&ndash;&gt;-->
    <!--<button :class="{'d-inline btn btn-outline-danger btn-sm':order.status === 'WAIT_BUYER_PAY'}">{{stateDesc}}</button>-->
    <!--</div>-->
    <!--</div>-->
  </div>
</template>

<script>
  export default {
    props: ['order'],
    name: 'OrderItem',
    computed: {
      stateDesc () {
        if (this.order) {
          switch (this.order.status) {
            case 'WAIT_BUYER_PAY':
              return this.$t('order-list-button.wait-buyer-pay')
              break
            case 'WAIT_SELLER_SEND_GOODS':
              return this.$t('order-list-button.wait-seller-send-goods')
              break
            case 'WAIT_BUYER_CONFIRM_GOODS':
              return this.$t('order-list-button.wait-buyer-confirm-goods')
              break
            case 'TRADE_BUYER_SIGNED':
              return this.$t('order-list-button.have-been-received')
              break
            default:
              return this.$t('order-list-button.have-been-canceled')
          }
        } else {
          return null
        }
      },

      statusClass () {
        if (this.order) {
          switch (this.order.status) {
            case 'WAIT_BUYER_PAY':
              return null
            case 'WAIT_SELLER_SEND_GOODS':
              return 'wait-send'
              break
            case 'WAIT_BUYER_CONFIRM_GOODS':
              return 'wait-confirm'
              break
            case 'TRADE_BUYER_SIGNED':
              return 'order-confirm'
              break
            default:
              return 'order-cancel'
          }
        }

        return null
      },

      buttonDesc () {
        if (this.order) {
          switch (this.order.status) {
            case 'WAIT_BUYER_PAY':
              return this.$t('buy-now')
            default:
              return this.$t('order-detail')
          }
        }
        return ''
      },
      skuDesc () {
        let sku = this.order.purchaseDetails[0].skuStructures
        if (sku && sku.length) {
          let desc = ''
          for (let index in sku) {
            let prop = sku[index]
            if (prop.category && prop.text) {
              desc += `${prop.category}:${prop.text} `
            }
          }
          return desc
        } else {
          return ''
        }
      }
    }
  }
</script>


<style scoped lang="less">
  .order-item {
    margin-bottom: 10px;
    height: 240px;

    .button {
      width: 330px;
      height: 44px;
      margin-top: 23px;
      background: @primary_color;
      color: white;
      font-size: @title_size_font;
      text-align: center;
      border-radius: 4px;
      border: none;
    }

    .status-flag {
      font-size: 11px;
      border-radius: 4px;
      padding-left: 6px;
      padding-right: 6px;
      line-height: 18px;
      border: 1px solid;
    }

    .wait-send {
      color: #ffb800;
      border-color: #ffb800;
      background: #fff4d6;
    }

    .wait-confirm {
      color: #8089cf;
      border-color: #8089cf;
      background: #ebecf7;
    }

    .order-cancel {
      color: #999999;
      border-color: #999999;
      background: #efefef;
    }

    .order-complete {
      color: #0aba69;
      border-color: #0aba69;
      background: #d8f4e7;
    }
  }

  .product {
    margin-top: 10px;
    text-align: left;
    .product-detail {
      background: white;
      padding: 12px;
      display: flex;
      flex-direction: row;
      font-size: @desc_size_font;

      img {
        width: 85px;
        height: 85px;
        margin-right: 10px;
        border-radius: 4px;
      }

      .desc {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        height: @desc_size_font*2+8;
        line-height: @desc_size_font+5;
        -webkit-box-orient: vertical;
        text-align: left;
        font-size: @desc_size_font;
        padding-right: 5px;
      }
      .sku {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        height: @sub_desc_size_font*2+8;
        line-height: @sub_desc_size_font+5;
        -webkit-box-orient: vertical;
        text-align: left;
        margin-top: 5px;
        margin-bottom: 5px;
        font-size: @sub_desc_size_font;
        color: @font_gray;
      }

      .sale-price {
        font-size: @large_size_font;
        line-height: @large_size_font;
      }
    }

    .product-price {
      margin-top: 1px;
      background: white;
      font-size: @desc_size_font;
      color: @font_gray;
      margin-left: 12px;
      margin-right: 12px;
      div {
        display: flex;
        flex-direction: row;
        div {
          flex-grow: 1
        }
      }

      .price-total {
        color: @primary_light_color;
        font-size: @largest_size_font;
        font-weight: bold;
      }
    }
  }


</style>



