<template>
  <div class="mobile_product_detail_prod">
    <v-btn color="#6f6f6f" fab small dark left fixed @click="$router.back()"
           absolute style="margin-top:15px;opacity:0.7;">
      <v-icon>arrow_back</v-icon>
    </v-btn>

    <div class="content">
      <v-carousel hide-delimiters v-if="product && product.galleryImgs &&product.galleryImgs.length" height="32rem"
                  :cycle="cycle">
        <v-carousel-item
          v-for="(item,i) in product.galleryImgs"
          :key="i"
          :src="item.url"
        />
      </v-carousel>

      <v-container v-if="product" grid-list-md fluid style="background: white">
        <v-layout row wrap>

          <v-flex xs12 class="product_title">
            {{product.title}}
          </v-flex>

          <v-flex xs12>
          <span v-if="product&&product.salePrice" class="sale_price">
            ${{product.salePrice}}
          </span>

            <span v-if="product&&product.originPrice" class="ori_price">
          ${{product.originPrice}}
          </span>
          </v-flex>
        </v-layout>

      </v-container>

      <div v-if="product" style="margin-top: 10px" class="index_item">
        <div class="index_content">
          <div class="index_flag"></div>
          {{$t('about-this-item')}}
        </div>
      </div>

      <div v-if="product" class="product_detail"
          v-html="product.detail">
      </div>

      <div class="bottom_button_container">
          <v-icon color="black" class="customer_service" >headset_mic</v-icon>
        <v-btn block color="primary" @click="showSku = true" :loading="!loaded"
               :disabled="!loaded">{{$t('buy-now')}}
        </v-btn>
      </div>

      <div v-if="recommendProducts && recommendProducts.length">
        <div style="margin-top: 10px" class="index_item">
          <div class="index_content">
            <div class="index_flag"></div>
            {{$t('Recommend')}}
          </div>
        </div>

        <v-container grid-list-xs fluid style="padding:0;">
          <v-layout row wrap>
            <v-flex xs6 lg4 xl3 v-for="(item,i) in recommendProducts"
                    @click="click(item)"
                    :key="i">
              <product-item v-bind:product='item'/>
            </v-flex>
          </v-layout>
        </v-container>
      </div>


      <div v-if="hotProducts && hotProducts.length">
        <div style="margin-top: 10px" class="index_item">
          <div class="index_content">
            <div class="index_flag"></div>
            {{$t('HotSale')}}
          </div>
        </div>

        <v-container grid-list-xs fluid style="padding:0;">
          <v-layout row wrap>
            <v-flex xs6 lg4 xl3 v-for="(item,i) in hotProducts"
                    @click="click(item)"
                    :key="i">
              <product-item v-bind:product='item'/>
            </v-flex>
          </v-layout>
        </v-container>
      </div>
    </div>

    <Sku v-if="product" v-model="showSku" :sku="skuData" @skuBuy="skuBuy" :title="product.title">
      <template slot-scope="props">
        <div class="bottom_sku_button">
          <v-btn block color="primary" @click="props.click">{{$t('continue')}}</v-btn>
        </div>
      </template>
    </Sku>

    <Loading v-model="showLoading"/>

    <Toast v-model="toastTip"/>

  </div>
</template>

<script>
  import Base from '@/components/page/ProductDetail/template/prod/Base'
  import NetWorkUtils from '@/util/Net'
  import {formatSku} from '@/util/Format'
  import {getUserInfo} from '@/util/DataUtil'
  import Sku from '@/components/view/Sku'
  import Loading from '@/components/view/Loading'
  import Toast from '@/components/view/Toast'
  import ProductItem from '@/components/view/ProductItem'
  import {productList} from '@/util/Net'
  import { mapState } from 'vuex';

  export default {
    name: "MobileProductDetail",
    extends: Base,
    components: {Sku, Loading, Toast, ProductItem},
    data() {
      return {
        loaded: false,
        showSku: false,
        skuData: null,
        cycle: true,
        showLoading: false,
        toastTip: '',
        recommendProducts: null,
        hotProducts: null,
      }
    },
     computed:mapState({
      product(state){
        if(this.productId){
          return state.productModule.products[this.productId]
        }else {
          return null
        }
      },
    }),
    methods: {
      async loadData() {
        this.$set(this, 'loaded', false)
        try {
          let info = await NetWorkUtils.post('/product/detail/v2', {'productId': this.productId})
          let product = info.product
          this.$store.commit('productModule/saveProduct',product)
          this.$set(this, "skuData", formatSku(product.skuInfo))
          let recommendReq = {}
          recommendReq.pageNo = 1
          recommendReq.pageSize = 4
          recommendReq.appId = window.midasConfig.appId

          productList(recommendReq).then(products=>{
            this.$set(this,'recommendProducts',products)
          })

          productList(recommendReq).then(products=>{
            this.$set(this,'hotProducts',products)
          })

        } catch (e) {
          midasLog.error(e)
        }

        this.$set(this, 'loaded', true)
      },
      async skuBuy(val) {
        this.showLoading = true

        try {
          await getUserInfo()
          this.product.skuBuy = val
          this.$router.push({name: 'OrderConfirm', params: {product: this.product}})
        } catch (e) {
          this.toastTip = e.message
        }

        this.showLoading = false

      }
    },

    mounted() {
      this.loadData()
    },

    activated() {
      this.$set(this, 'cycle', true)
    },

    deactivated() {
      this.$set(this, 'cycle', false)
    }
  }
</script>

<style scoped lang="less">
  .mobile_product_detail_prod {
    height: 100%;
    .content {
      margin-bottom: @bottom_button_height;
    }

    .product_title {
      font-size: @title_size_font;
    }

    .sale_price {
      font-size: @largest_size_font;
      font-weight: bold;
      margin-right: 10px;
    }

    .ori_price {
      font-size: @title_size_font;
      color: @font_gray;
      text-decoration: line-through;
    }

    .product_detail {
      background: white;
      margin-top: 1px;
    }

    .customer_service{
      height:44px;
      width: 44px;
      margin-right: 12px;
      border-radius: 4px;
      border: 1px solid @background_grey;
    }
  }

  .bottom_sku_button {
    height: @bottom_button_height;
    width: 100%;
    display: flex;
    background: white;
    justify-content: center;
    align-items: center;
    padding: 12px;
    .v-btn {
      height: 100%;
    }
  }


</style>



