<template>
  <div class="category_template">
    <v-container style="width: 110%;padding: 0 0 10px;" text-xs-center>
      <v-layout row  v-for="row in getRowNum()" :key="row">
        <div class="flex xs3" style="overflow: hidden" v-for="(item,i) in getColumnObjects(row)" :key="i">
          <v-img :src="item.img" aspect-ratio="1" style="margin: 5px 16px;"></v-img>
          <div style="font-size: 12px;padding-bottom: 5px;">{{item.title}}</div>
        </div>
      </v-layout>
    </v-container>
  </div>

</template>

<script>
  let COLUMN_NUM = 5
  export default {
    name: "CategoryTemplate",
    props:{
      template:null
    },

    methods:{
      getRowNum(){
        if (this.template&&this.template.subitems){
          return Math.ceil(this.template.subitems.length/COLUMN_NUM)
        }else {
          return 0
        }
      },

      getColumnObjects(row){
        let start = (row -1)*COLUMN_NUM
        let end = (row)*COLUMN_NUM
        end = end>this.template.subitems.length?this.template.subitems.length:end
        return  this.template.subitems.slice(start,end)
      }
    }
  }
</script>

<style scoped lang="less">

  .category_template {
    overflow: scroll;
    background: white;
  }

  .category_template::-webkit-scrollbar {
    display: none;
  }

</style>



