<template>
  <div>
    <div class="index_item">
      <div class="index_content">
        <div class="index_flag"></div>
        {{$t('Daily Featured')}}

        <v-btn light color="white" small style="margin-left:auto;">More</v-btn>
      </div>
    </div>


    <v-container grid-list-xs style="padding: 0;"  fluid>
      <v-layout row wrap>
        <v-flex xs6 v-for="(item,i) in template.subitems" :key="i">
          <div style="display: flex;flex-direction: row;padding: 10px;background: white">
            <v-img :src="item.galleryImgs[0].url" aspect-ratio="1" style="width: 50%"></v-img>
            <v-layout style="padding-left: 5px;margin: 0" column justify-space-around>
              <div class="product_name">
                {{item.productName}}
              </div>

              <div style="font-size: 15px;font-weight: bold">
                ${{item.salePrice}}
              </div>
            </v-layout>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
  export default {
    name: "DailyTemplate",
    props: {
      template: null
    },
  }
</script>

<style scoped lang="less">

  .product_name{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    height: 28px;
    line-height: 14px;
    font-size: 14px;
  }
</style>



