import Vue from 'vue'
import Router from 'vue-router'
import Navigation from 'vue-navigation'
import Home from '../components/page/Home/Home'
import ProductDetail from '../components/page/ProductDetail/ProductDetail'
import OrderConfirm from '../components/page/OrderConfirm/OrderConfirm'
import OrderList from '../components/page/OrderList/OrderList'

const router = new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component:  resolve => require(['@/components/page/HelloWorld/HelloWorld'], resolve),//
    },
    {
      path: '/',
      name: 'Home',
      component:  Home,
    },
    {
      path: '/ProductDetail',
      name: 'ProductDetail',
      component:  ProductDetail,
    },
    {
      path: '/OrderConfirm',
      name: 'OrderConfirm',
      component:  OrderConfirm,
    },
    {
      path: '/OrderList',
      name: 'OrderList',
      component:  OrderList,
    }
  ]
})

Vue.use(Router)
Vue.use(Navigation, {router})


export default router



