<template>
  <v-container class="product_detail_prod" grid-list-lg fluid>
    <v-layout row wrap>
      <v-flex lg4 >
        <v-carousel hide-delimiters
                    v-if="product && product.galleryImgs &&product.galleryImgs.length"
                    height="300">
          <v-carousel-item
            v-for="(item,i) in product.galleryImgs"
            :key="i"
            :src="item.url"
          />
        </v-carousel>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import Base from '@/components/page/ProductDetail/template/prod/Base'

  export default {
    name: "WebProductDetail",
    extends:Base
  }
</script>

<style scoped>

</style>



