
window.midasLog = {
  info(){
    console.info.apply(console,arguments)
  },
  debug(){
    if(process.env.NODE_ENV!=="production"||midasConfig.forceLog){
      console.info.apply(console,arguments)
    }
  },
  error(){
    console.error.apply(console,arguments)
  },
}

if(!window.midasConfig){
  window.midasConfig = {}
}

window.midasConfig.theme = {
  primary: "#0C84FF",
  secondary: "#9FA8DA",
  accent: "#9c27b0",
  error: "#f44336",
  warning: "#ffeb3b",
  info: "#2196f3",
  success: "#4caf50",
  customColor:'#444f50'
}

let temp = window.location.href.split('?')
if(temp&&temp[1]){
  temp = temp[1].split('&')
  if(temp&&temp.length){
    for(let index in temp){
      if(temp[index].indexOf('appId')>=0){
        window.midasConfig.appId = temp[index].split('=')[1]
        break
      }
    }
  }
}


if(midasConfig.appId){
  midasLog.info('appId',midasConfig.appId)
}else {
  midasLog.error('no appId')
}
midasConfig.layout = 'prode'
midasConfig.version = process.env.NODE_VERSION

function resize(){
//viewport
  let docEl = document.documentElement
  docEl.style.fontSize = docEl.clientWidth / 32 + 'px'

  midasLog.info('devicePixelRatio',window.devicePixelRatio)
  midasLog.info('clientWidth',docEl.clientWidth)
  midasLog.info('fontSize',docEl.style.fontSize)
}

resize()

window.onresize=()=>{
  resize()
}


midasLog.info('window width',window.screen.width)
midasLog.info('env',process.env.NODE_ENV)
midasLog.info('version',process.env.NODE_VERSION)
midasLog.info('layout template',midasConfig.layout)

require ('./Statistic')




