<template>
  <pull-refresh v-model="isRefreshing"
                @refresh="onRefresh">
    <load-more v-model="isLoadingMore"
               :finished="noMore"
               @load="onLoadMore">
      <slot :items="items"/>
    </load-more>
  </pull-refresh>
</template>

<script>
  import PullRefresh from '@/components/view/PullRefresh'
  import LoadMore from '@/components/view/LoadMore'

  const PAGE_SIZE = 20
  export default {
    name: "RefreshAndLoadMore",
    components:{PullRefresh,LoadMore},
    data(){
      return {
        isLoadingData:false,
        isRefreshing:false,
        isLoadingMore:false,
        noMore:false,
        items:null
      }
    },
    methods:{
      async onRefresh(){
        this.loadDataByPage(1,PAGE_SIZE)
      },
      async onLoadMore(){
        let page = this.items?(this.items.length/PAGE_SIZE+1):1
        this.loadDataByPage(page,PAGE_SIZE)
      },

      async loadDataByPage(page,pageSize){
        if(this.isLoadingData){
          return
        }

        this.isLoadingData = true
        try{
          let parent = this.$parent
          let loadDataByPage = parent['loadDataByPage']
          while (!loadDataByPage&&parent) {
            parent = parent.$parent === parent?null:parent.$parent
            loadDataByPage = parent?parent['loadDataByPage']:null
          }

          let newItems = await loadDataByPage(page,pageSize)
          this.noMore = !(newItems&&newItems.length===pageSize)
          if(page === 1){
            this.items = newItems
          }else {
            this.items = this.items?this.items:[]
            if(newItems&&newItems.length){
              this.items = this.items.concat(newItems)
            }
          }

          this.$set(this,'items',this.items)
        }catch (e) {
          midasLog.error(e)
        }

        setTimeout(()=>{
          this.isLoadingData = false
          this.isRefreshing = false
          this.isLoadingMore = false
        },300)

      }
    },
  }
</script>

<style scoped>

</style>



