<template>
  <div class="pull-refresh">
    <div
      class='track'
      :style="style"
      @touchstart="onTouchStart"
      @touchmove="onTouchMove"
      @touchend="onTouchEnd"
      @touchcancel="onTouchEnd"
    >
      <v-container class="head" column>
        <slot v-if="status === 'normal'" name="normal"/>
        <slot v-if="status === 'pulling'" name="pulling">
          <span class="text">{{ pullingText || 'Pull To Refresh'}}</span>
        </slot>
        <slot v-if="status === 'loosing'" name="loosing">
          <span class="text">{{ loosingText || 'Loose To Refresh' }}</span>
        </slot>
        <v-layout class="loading" fill-height align-center justify-center>
          <slot v-if="status === 'loading'" name="loading">
            <v-progress-circular indeterminate color="grey" :size="16" :width="2"></v-progress-circular>
            <v-flex shrink class="text" ml-3>{{ loosingText || 'Loading' }}</v-flex>
          </slot>
        </v-layout>
      </v-container>
      <slot/>
    </div>
  </div>
</template>

<script>
  import scrollUtils from '../../util/scroll';
  import Touch from '../../util/touch';

  export default ({
    name: 'PullRefresh',

    mixins: [Touch],

    props: {
      disabled: Boolean,
      pullingText: String,
      loosingText: String,
      loadingText: String,
      value: {
        type: Boolean,
        required: true
      },
      animationDuration: {
        type: Number,
        default: 300
      },
      headHeight: {
        type: Number,
        default: 50
      }
    },

    data() {
      return {
        status: 'normal',
        height: 0,
        duration: 0
      };
    },

    computed: {
      style() {
        return {
          transition: `${this.duration}ms`,
          transform: `translate3d(0,${this.height}px, 0)`
        };
      },

      untouchable() {
        return this.status === 'loading' || this.disabled;
      }
    },

    mounted() {
      this.scrollEl = scrollUtils.getScrollEventTarget(this.$el);
    },

    watch: {
      value(val) {
        this.duration = this.animationDuration;
        this.getStatus(val ? this.headHeight : 0, val);
      }
    },

    methods: {
      onTouchStart(event) {
        if (this.untouchable) {
          return;
        }
        if (this.getCeiling()) {
          this.duration = 0;
          this.touchStart(event);
        }
      },

      onTouchMove(event) {
        if (this.untouchable) {
          return;
        }

        this.touchMove(event);

        if (!this.ceiling && this.getCeiling()) {
          this.duration = 0;
          this.startY = event.touches[0].clientY;
          this.deltaY = 0;
        }

        if (this.ceiling && this.deltaY >= 0) {
          if (this.direction === 'vertical') {
            this.getStatus(this.ease(this.deltaY));
            event.cancelable && event.preventDefault();
          }
        }
      },

      onTouchEnd() {
        if (this.untouchable) {
          return;
        }

        if (this.ceiling && this.deltaY) {
          this.duration = this.animationDuration;
          if (this.status === 'loosing') {
            this.getStatus(this.headHeight, true);
            this.$emit('input', true);
            this.$emit('refresh');
          } else {
            this.getStatus(0);
          }
        }
      },

      getCeiling() {
        this.ceiling = scrollUtils.getScrollTop(this.scrollEl) === 0;
        return this.ceiling;
      },

      ease(height) {
        const {headHeight} = this;
        return height < headHeight
          ? height
          : height < headHeight * 2
            ? Math.round(headHeight + (height - headHeight) / 2)
            : Math.round(headHeight * 1.5 + (height - headHeight * 2) / 4);
      },

      getStatus(height, isLoading) {
        this.height = height;

        const status = isLoading
          ? 'loading' : height === 0
            ? 'normal' : height < this.headHeight
              ? 'pulling' : 'loosing';

        if (status !== this.status) {
          this.status = status;
        }
      }
    }
  });
</script>

<style scoped lang="less">
  .pull-refresh {
    user-select: none;
    overflow: hidden;

    .track {
      position: relative;
    }

    .head {
      width: 100%;
      height: 50px;
      left: 0;
      overflow: hidden;
      position: absolute;
      text-align: center;
      top: -50px;
      font-size: 14px;
      color: black;
      line-height: 50px;
    }

    .loading {
      .van-loading {
        width: 16px;
        height: 16px;
        margin-right: 5px;
      }

      span,
      .van-loading {
        vertical-align: middle;
        display: inline-block;
      }
    }

    .text {
      display: block;
      color:gray
    }
  }
</style>



