import Vue from 'vue'
import Vuex from 'vuex'
import productModule from '../store/modules/productModule'

const debug = process.env.NODE_ENV !== 'production'

Vue.use(Vuex);

export default new Vuex.Store({
    modules: {
        productModule
    },
    strict: debug,
})


