export default function (path) {
  if(path){
    let baseComponent = path.indexOf('page/')===0?require('@/components/BasePageComponent'):require('@/components/BaseViewComponent')
    return {
      mixins:[baseComponent.default,require('@/util/TemplateUtil').getTemplate(path)],
    }
  }else {
    throw new Error('path is null')
  }
}



