const state = {
    products: {}
}

const getters = {

}

const mutations = {
  saveProduct(state, product) {
    let temp = state.products
    state.products = null
    temp[product.productId] = product
    state.products = temp
  },
}
const actions = {}


export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



