<template>
  <div>
    <v-toolbar color="primary" dark fixed app>
      <v-btn icon @click="$router.back()">
        <v-icon>arrow_back_ios</v-icon>
      </v-btn>
      <v-toolbar-title>Order Confirm</v-toolbar-title>
    </v-toolbar>

    <div class="address" v-on:click="goAddress">
      <div class="index_item">
        <div class="index_content">
          <div class="index_flag"></div>
          {{$t('Shipping Address')}}
        </div>
      </div>

      <div class="address-detail">
        <v-icon medium class="text--lighten-3" color="primary" style="margin-right: 10px">location_on</v-icon>

        <div style="flex-grow: 1;text-align: left">
          <div v-if="address" style="display: flex;flex-direction: row">
            <div class="name">{{address.name}}</div>
            <div class="phone">{{address.phone}}</div>
          </div>

          <div v-if="address" class="detail">{{address.address}}</div>

          <div class="detail primary--text">Add a new address</div>

          <div></div>
        </div>

        <v-icon medium>chevron_right</v-icon>
      </div>
    </div>

    <div style="margin-top: 10px" class="shipping">
      <div class="index_item">
        <div class="index_content">
          <div class="index_flag"></div>
          {{$t('Shipping Method')}}
        </div>
      </div>


      <v-container style="padding: 10px;background: white">
        <v-layout row wrap style="font-size: 10px">
          <v-flex xs3 style="color: gray;">
            Delivery Time
          </v-flex>

          <v-flex xs3 style="color: gray;">
            Cost ( US $ )
          </v-flex>

          <v-flex xs5 style="color: gray;">
            Carrier
          </v-flex>

          <v-flex xs1 style="color: gray;">
          </v-flex>
        </v-layout>
      </v-container>

      <v-container style="padding: 0;background: white" v-if="shippingInfos && shippingInfos.length">
        <v-layout @click="setChosenShip(shippingInfo)" style="font-size: 10px;padding: 10px" row wrap ripple v-ripple>
          <v-flex xs3>
            {{shippingInfo.deliverTime}}{{$t('days')}}
          </v-flex>

          <v-flex xs3>
            {{shippingInfo.price+'$'}}
          </v-flex>

          <v-flex xs5>
            {{shippingInfo.shipCompany}}
          </v-flex>

          <v-flex xs1>

            <v-icon :color="chosenShip===item?'primary':'grey'">
              check_circle
            </v-icon>

          </v-flex>
        </v-layout>

        <v-layout @click="setChosenShip(item)" style="font-size: 10px;padding: 10px" row wrap ripple v-ripple
                  v-for="(item,i) in shippingInfos" :key="i">
          <v-flex xs3>
            {{item.deliverTime}}{{$t('days')}}
          </v-flex>

          <v-flex xs3>
            {{item.price+'$'}}
          </v-flex>

          <v-flex xs5>
            {{item.shipCompany}}
          </v-flex>

          <v-flex xs1>

            <v-icon :color="chosenShip===item?'primary':'grey'">
              check_circle
            </v-icon>

          </v-flex>
        </v-layout>

      </v-container>

    </div>

    <div v-if="product" class="product">

      <div class="item-detail">
        <div class="flag"/>
        <div class="item-detail-title">{{$t('review-your-order')}}</div>
      </div>

      <div class="product-detail">
        <img :src="product.productUrl">
        <div style="flex-grow: 1">
          <div class="desc">{{product.title}}</div>
          <div class="sku">{{product.skuDesc}}</div>
          <div>
            <div class="sale-price">${{product.skuBuy.salePrice}}</div>
          </div>
        </div>
        <div style="color: gray;">x{{product.skuBuy.selectNum}}</div>
      </div>

      <div class="product-price">

        <div>
          <div>{{$t('price')}}:</div>
          <span>${{(product.skuBuy.salePrice*product.skuBuy.selectNum).toFixed(2)}}</span>
        </div>

        <div>
          <div style="margin-top: 0.5rem;margin-bottom: 0.5rem">{{$t('shipping-price')}}:</div>
          <span style="margin-top: 0.5rem;margin-bottom: 0.5rem">
              ${{chosenShip?chosenShip.price:0}}
            </span>
        </div>

        <div>
          <div>{{$t('total-price')}}</div>
          <span class="price-total">
              {{orderTotal}}
            </span>
        </div>

      </div>
    </div>

    <div class="bottom_button">
      <v-btn block color="primary">{{$t('buy-now')}}
      </v-btn>
    </div>

  </div>
</template>

<script>
  import {getUserInfo} from '@/util/DataUtil'
  import NetWorkUtils from '@/util/Net'


  export default {
    name: "order_confirm_prod",
    data() {
      return {
        product: null,
        address: null,
        shippingInfos: null,
        shippingPrice: 0,
        chosenShip: null,
        shippingInfo:null
      }
    },

    methods: {
      setProduct() {
        let product = this.$route.params.product
        let skuDesc = ""
        if (!product.skuDesc && product.skuBuy.category) {
          let keys = Object.keys(product.skuBuy.category)
          for (let index in keys) {
            skuDesc = `${skuDesc}${keys[index]}:${product.skuBuy.category[keys[index]].text} `
          }
        }

        product.skuDesc = skuDesc
        if (!product.productUrl) {
          product.productUrl = product.galleryImgs[0].url
        }

        this.$set(this, 'product', product)
      },

      async setAddress() {
        let user = await getUserInfo()
        let address = user.addresses.chosenAddress
        this.$set(this, 'address', address)
        this.setShippingInfo(address)
      },

      async setShippingInfo(address) {
        try {
          this.$set(this, 'shippingInfos', null)
          let req = {}
          req.productId = this.product.productId
          req.countryCode = address.countryCode
          let info = await NetWorkUtils.post('/product/shippinginfo/v1', req)
          this.$set(this, 'shippingInfos', info.shippingInfos)
          this.$set(this, 'chosenShip', info.shippingInfos[0])
        } catch (e) {
          midasLog.error(e)
          this.$set(this, 'shippingInfos', [])
        }
      },

      goAddress() {
      },
      setChosenShip(ship) {
        this.$set(this, 'chosenShip', ship)
      }
    },

    computed: {
      orderTotal() {
        let total = this.product.skuBuy.salePrice * this.product.skuBuy.selectNum
        if (this.chosenShip) {
          total += Number(this.chosenShip.price)
        }

        return total.toFixed(2)
      }
    },

    mounted() {
      this.setAddress()
      this.setProduct()
    }
  }
</script>

<style scoped lang="less">
  .address {
    background: white;
    padding-bottom: 7px;

    .address-detail {
      display: flex;
      flex-direction: row;
      align-items: center;

      img {
        margin-left: 11px;
        margin-right: 7px;
      }

      .name {
        font-size: @subtitle_size_font;
        margin-right: 12px;
      }

      .phone {
        font-size: @desc_size_font;
        color: @font_gray;
      }

      .detail {
        font-size: @subtitle_size_font;
      }

    }
  }

  .item-detail {
    background: white;
    text-align: left;
    display: flex;
    flex-direction: row;
    align-items: center;
    .item-detail-title {
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 40px;
      font-size: @title_size_font;
      margin-bottom: 1px;
    }

    .flag {
      height: 24px;
      width: 3px;
      margin-right: 12px;
      background: @primary_color;
    }
  }

  .product {
    margin-top: 10px;
    text-align: left;
    .product-detail {
      background: white;
      padding: 12px;
      display: flex;
      flex-direction: row;
      font-size: @desc_size_font;

      img {
        width: 80px;
        height: 80px;
        margin-right: 10px;
        border-radius: 4px;
      }

      .desc {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        height: @desc_size_font*2+8;
        line-height: @desc_size_font+5;
        -webkit-box-orient: vertical;
        text-align: left;
        font-size: @desc_size_font;
        padding-right: 5px;
      }
      .sku {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        height: @sub_desc_size_font*2+8;
        line-height: @sub_desc_size_font+5;
        -webkit-box-orient: vertical;
        text-align: left;
        margin-top: 5px;
        margin-bottom: 5px;
        font-size: @sub_desc_size_font;
        color: @font_gray;
      }

      .sale-price {
        font-size: @large_size_font;
        line-height: @large_size_font;
      }
    }

    .product-price {
      margin-top: 1px;
      background: white;
      font-size: @desc_size_font;
      color: @font_gray;
      padding: 15px 12px;
      div {
        display: flex;
        flex-direction: row;
        div {
          flex-grow: 1
        }
      }

      .price-total {
        color: black;
        font-size: @largest_size_font;
        font-weight: bold;
      }
    }
  }

  .shipping {
  }

  .bottom_button {
    height: @bottom_button_height;
    width: 100%;
    z-index: 1;
    position: fixed;
    bottom: 0;
    display: flex;
    background: white;
    justify-content: center;
    align-items: center;
    padding: 10px;
    .v-btn {
      height: 100%;
    }
  }

</style>



