const dimensionMap = {
  'web_version':'dimension1',
  'app_id':'dimension2'
};

(function init() {
  let id = 'UA-128195364-3'
  midasLog.debug('statistic init')
  window.document.write("<scr"+`ipt async src='https://www.googletagmanager.com/gtag/js?id=${id}'></scr`+"ipt>")
  window.dataLayer = window.dataLayer || [];
  function gtag(){
    dataLayer.push(arguments);
  }
  window.gtag = gtag
  gtag('js', new Date());

  gtag('config', id);

  gtag('set', 'dimension1', 'web_version');
  gtag('set', {
    'currency': 'USD',
    'app_name': 'midas-web',
    'app_key': 'test app id',
    'web_version': midasConfig.version
  });
}())


export function setBaseValue(key,value) {
  gtag('set',{key,value})
}


export const PRODUCTION_EXCEPTION = 'production_exception'
export const PRODUCTION_COUNT_TIME = 'production_count_time'
const recordHelper = {
  [PRODUCTION_EXCEPTION]:function (value) {
    let level = value['level']
    let error = value['error']
    recordError(level,error)
  }
}

let test ={
  'title':'home',
  'path':'/home'
}

//level 1, 2, 3,
function recordError(level,error) {
  gtag('event', 'exception', {
    'description': error,
    'fatal': level>2,
  });
  midasLog.debug(error)
}

window.midasRecord = function (type,params) {
  let f = recordHelper[type]
  if(f){
    f(params)
  }else {
    recordError(1,`no this type statistic:${type}`)
  }
}



