<template>
  <div class="home_prod">
    <v-toolbar color="primary" dark fixed app>
      <v-btn icon>
        <v-icon>arrow_back_ios</v-icon>
      </v-btn>
      <v-toolbar-title>Title</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon @click="goOrder()">
        <v-icon>description</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>search</v-icon>
      </v-btn>
    </v-toolbar>

    <refresh-and-load-more>
      <template slot-scope="loadData">
        <template-container v-if="template && template.categories" :categories = "template.categories"/>
        <v-container grid-list-xs fluid style="padding:0">
          <v-layout row wrap v-if="loadData.items">
            <v-flex xs6 lg4 xl3 v-for="(item,i) in loadData.items"
                    @click="click(item)"
                    :key="i">
              <product-item v-bind:product='item'/>
            </v-flex>
          </v-layout>
        </v-container>
      </template>
    </refresh-and-load-more>
  </div>
</template>

<script>
  import RefreshAndLoadMore from '@/components/view/RefreshAndLoadMore'
  import ProductItem from '@/components/view/ProductItem'
  import TemplateContainer from "@/components/page/Home/template/prod/TemplateContainer"
  import NetWorkUtils from '@/util/Net'
  import {getTemplate} from '@/util/Net'


  export default {
    name: "home_prod",
    components: {RefreshAndLoadMore, ProductItem,TemplateContainer},
    data: () => {
      return {
        template: null,
        cycle:true
      }
    },
    methods: {
      async loadDataByPage(page, pageSize) {
        let req = {}
        req.pageNo = page
        req.pageSize = pageSize
        req.appId = window.midasConfig.appId
        let info = null
        if (page === 1) {
          let template = await getTemplate(window.midasConfig.appId)
          this.$set(this,'template',template)
          if(template.categories && template.categories.length){
            for (let i in template.categories)  {
              if (template.categories[i].type === 'product') {
                info = await NetWorkUtils.post('/product/list/v2', req)
                break
              }
            }
          }
        }else {
          info = await NetWorkUtils.post('/product/list/v2', req)
        }
        return info ? info.products : []
      },
      click(item) {
        this.$router.push({name: 'ProductDetail', params: item})
      },
      goOrder(){
        this.$router.push({name: 'OrderList'})
      }
    },
    activated(){
      this.$set(this,'cycle',true)
    },

    deactivated(){
      this.$set(this,'cycle',false)
    }
  }
</script>

<style scoped lang="less">
  .home_prod {

  }
</style>



